import { Injectable } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { AdsApi } from '@core/data-access/apis/ads.api';
import { AuthApi } from '@core/data-access/apis/auth.api';
import { AgentInvitationState } from '@core/data-access/states/agent-invitation.state';
import { ConfigState } from '@core/data-access/states/config.state';
import { SectionsState } from '@core/data-access/states/sections.state';
import { AisleType, SectionType } from '@core/enums';
import { LocaleType } from '@core/enums/locale.enum';
import { Ad, AgentInvitation, Aisle, Seller, VerifyEmailInvitationData } from '@core/models';
import { Translations } from '@core/models/translations.model';
import { BehaviorSubject, Observable, combineLatest, distinctUntilChanged, forkJoin, map, takeWhile } from 'rxjs';
import { AcceptInviteDialogComponent } from './components/accept-invite-dialog/accept-invite-dialog.component';
import { UnableAcceptInviteDialogComponent } from './components/unable-accept-invite-dialog/unable-accept-invite-dialog.component';
import { AgentsApi } from '@core/data-access/apis/agents.api';

export interface HomeViewModel {
	adsForRent: {
		rent: Ad[];
		share: Ad[];
		commercial: Ad[];
	};
	adsForSell: {
		rent: Ad[];
		commercial: Ad[];
	};
	aisleList: Aisle[];
}

interface HomeLatestAds {
	adsForRent: {
		rent: Ad[];
		share: Ad[];
		commercial: Ad[];
	};
	adsForSell: {
		rent: Ad[];
		commercial: Ad[];
	};
}

// TODO: Create a seperate state if needed
@Injectable({ providedIn: 'root' })
export class HomeFacade {
	constructor(
		private readonly configState: ConfigState,
		private readonly agentApi: AgentsApi,
		private readonly adsApi: AdsApi,
		private sectionState: SectionsState,
		private agentInvitationState: AgentInvitationState,
		private authApi: AuthApi,
		private dialog: MatDialog
	) {}

	private readonly _aiseList$ = new BehaviorSubject<Aisle[]>([]);
	private readonly _latestAds$ = new BehaviorSubject<HomeLatestAds>(undefined);

	getLocale$(): Observable<LocaleType> {
		return this.configState.locale$;
	}

	getTranslations$(): Observable<Translations> {
		return this.configState.translations$;
	}

	getAisleList$(): Observable<Aisle[]> {
		return this._aiseList$.asObservable();
	}

	getHomeVm$(): Observable<HomeViewModel> {
		return combineLatest({
			latestAds: this._latestAds$.pipe(distinctUntilChanged()),
			aisleList: this._aiseList$.pipe(distinctUntilChanged()),
		}).pipe(
			map(({ latestAds, aisleList }) => {
				return {
					...latestAds,
					aisleList,
				};
			})
		);
	}

	loadLatestAds(): void {
		forkJoin({
			rent: this.adsApi.getLatestAdList({ section: SectionType.PROPERTIES_FOR_RENT, aisle: AisleType.RENT_RESIDENTIAL, perPage: 5 }),
			share: this.adsApi.getLatestAdList({ section: SectionType.PROPERTIES_FOR_RENT, aisle: AisleType.SHARE_RESIDENTIAL, perPage: 5 }),
			commercial: this.adsApi.getLatestAdList({ section: SectionType.PROPERTIES_FOR_RENT, aisle: AisleType.RENT_COMMERCIAL, perPage: 5 }),
			rentForSell: this.adsApi.getLatestAdList({ section: SectionType.PROPERTIES_FOR_SALE, aisle: AisleType.SALE_RESIDENTIAL, perPage: 5 }),
			commercialForSell: this.adsApi.getLatestAdList({ section: SectionType.PROPERTIES_FOR_SALE, aisle: AisleType.SALE_COMMERCIAL, perPage: 5 }),
		})
			.pipe(
				map(({ rent, share, commercial, rentForSell, commercialForSell }) => {
					return {
						adsForRent: {
							rent,
							share,
							commercial,
						},
						adsForSell: {
							rent: rentForSell,
							commercial: commercialForSell,
						},
					};
				})
			)
			.subscribe((response: HomeLatestAds) => this._latestAds$.next(response));
	}

	loadLatestAdsList(): void {
		this.adsApi
			.getLatestAds()
			.pipe(
				map(({ data }) => {
					return {
						adsForRent: {
							rent: data.find((i) => i.reference === 'rent-residential').ads,
							share: data.find((i) => i.reference === 'share-residential').ads,
							commercial: data.find((i) => i.reference === 'rent-commercial').ads,
						},
						adsForSell: {
							rent: data.find((i) => i.reference === 'sell-residential').ads,
							commercial: data.find((i) => i.reference === 'sell-commercial').ads,
						},
					};
				})
			)
			.subscribe((response) => this._latestAds$.next(response));
	}

	loadAisles(section: SectionType): void {
		this.sectionState
			.getSortedAisleList$(section)
			.pipe(takeWhile((aisleList) => !!aisleList))
			.subscribe((aisleList) => this._aiseList$.next(aisleList));
	}

	isNewEmailInvitation(agentInvitationData: AgentInvitation): boolean {
		const PREV_DATA = this.agentInvitationState.getEmailInvitation();
		const IS_OLD_INVITATION = JSON.stringify(PREV_DATA) === JSON.stringify(agentInvitationData);

		return !IS_OLD_INVITATION;
	}

	setEmailInvitation(agentInvitation: AgentInvitation): void {
		this.agentInvitationState.setEmailInvitaion(agentInvitation);
	}

	verifyEmailInvitation(agentInvitation: AgentInvitation): void {
		this.authApi.verifyEmailInvitation(agentInvitation).subscribe((response: { data: VerifyEmailInvitationData }) => {
			const CONFIG_DATA = {
				width: '500px',
				height: 'auto',
				disableClose: true,
				maxHeight: 'unset',
				maxWidth: 'unset',
				panelClass: 'email-invitation',
			};

			const DATA = {
				invitationLinkData: agentInvitation,
				inviterData: response?.data,
			};

			//if no business account show Accept pop up
			if (response.data?.hasBusinessProfile === '0') {
				this.dialog.open(AcceptInviteDialogComponent, { data: DATA, ...CONFIG_DATA });
			} else {
				//if user has already a business account  show Unable accept pop up
				this.dialog.open(UnableAcceptInviteDialogComponent, CONFIG_DATA);
			}
		});
	}

	getBusiness$(): Observable<Seller[]> {
		return this.agentApi.getBusinessList();
	}
}
