import { Injectable } from '@angular/core';
import { AgentInvitation } from '@core/models';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
	providedIn: 'root',
})
export class AgentInvitationState {
	constructor() {}

	private _emailInvitaion$ = new BehaviorSubject<AgentInvitation>(undefined);

	get emailInvitaion$(): Observable<AgentInvitation> {
		return this._emailInvitaion$.asObservable();
	}

	getEmailInvitation(): AgentInvitation {
		return this._emailInvitaion$.getValue();
	}

	setEmailInvitaion(agentInvitation: AgentInvitation): void {
		this._emailInvitaion$.next(agentInvitation);
	}
}
