import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { SnackService } from '@core/services/snack.service';
import { ButtonType } from '@shared/components/button/button.component';
import { DeviceDetectorService } from 'ngx-device-detector';

@Component({
    selector: 'findqo-unable-accept-invite-dialog',
    templateUrl: './unable-accept-invite-dialog.component.html',
    styleUrls: ['./unable-accept-invite-dialog.component.scss'],
    standalone: false
})
export class UnableAcceptInviteDialogComponent {
	constructor(
		public dialogRef: MatDialogRef<UnableAcceptInviteDialogComponent>,
		private deviceDetectorService: DeviceDetectorService,
		private snackbar: SnackService
	) {}
	readonly buttonType = ButtonType;

	onClose(): void {
		this.dialogRef.close();
	}

	call(): void {
		navigator.clipboard.writeText('+353-19061509');

		if (this.deviceDetectorService.isDesktop()) {
			this.snackbar.open({
				data: { translationKey: 'n712', type: 'success' },
				panelClass: 'snackbar-success',
			});
		} else {
			window.location.href = 'tel:+353-19061509';
		}
	}
}
