import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { AgentInvitation, VerifyEmailInvitationData } from '@core/models';
import { AuthFacade } from '@feature/auth/auth.facade';
import { ButtonType } from '@shared/components/button/button.component';
export interface DialogData {
	invitationLinkData: AgentInvitation;
	inviterData: VerifyEmailInvitationData;
}

@Component({
    selector: 'findqo-accept-invite-dialog',
    templateUrl: './accept-invite-dialog.component.html',
    styleUrls: ['./accept-invite-dialog.component.scss'],
    standalone: false
})
export class AcceptInviteDialogComponent {
	constructor(
		@Inject(MAT_DIALOG_DATA) public agentData: DialogData,
		public dialogRef: MatDialogRef<AcceptInviteDialogComponent>,
		private authFacade: AuthFacade
	) {}

	readonly buttonType = ButtonType;

	onAccept(): void {
		this.authFacade.acceptAgentInvitation(this.agentData?.invitationLinkData, this.agentData?.inviterData?.inviter?.name);
		this.dialogRef.close();
	}

	onDecline(): void {
		this.authFacade.rejectAgentInvitation(this.agentData?.invitationLinkData, this.dialogRef);
	}
}
