import { AfterViewInit, Component, ElementRef, ViewChild } from '@angular/core';
import { ViewportService } from '@core/services/viewport.service';
import { HomeFacade } from '@feature/home/home.facade';
import { register } from 'swiper/element';
register();
@Component({
	selector: 'findqo-advertise-agent',
	templateUrl: './advertise-agent.component.html',
	styleUrl: './advertise-agent.component.scss',
	standalone: false,
})
export class AdvertiseAgentComponent implements AfterViewInit {
	@ViewChild('swiperSlideShow', { static: false }) swiperEl!: ElementRef;
	constructor(
		private readonly homeFacade: HomeFacade,
		protected viewport: ViewportService,
	) {}

	readonly bussinessListVm$ = this.homeFacade.getBusiness$();

	ngAfterViewInit() {
		if (this.swiperEl) {
			const swiperElement = this.swiperEl.nativeElement;
			Object.assign(swiperElement, {
				slidesPerView: this.viewport.desktop ? 2.9 : 1,
				spaceBetween: this.viewport.desktop ? 0 : 100,
				loop: true,
				// autoplay: {
				// 	delay: 3000,
				// 	disableOnInteraction: false,
				// },
				navigation: {
					nextEl: document.querySelector('.swiper-button-next'),
					prevEl: document.querySelector('.swiper-button-prev'),
				},
			});
			swiperElement.initialize();
		}
	}
}
